.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  color: #000;
  overflow-x: hidden;
  height: 100%;
  background-color: #fff;
  background-repeat: no-repeat;
}
.plus-minus {
  position: relative;
}
.plus {
  position: absolute;
  top: -4px;
  left: 2px;
  cursor: pointer;
}
.minus {
  position: absolute;
  top: 8px;
  left: 5px;
  cursor: pointer;
}
.vsm-text:hover {
  color: #ff5252;
}
.bookmodal {
  width: 200px;
  height: 250px;
  border-radius: 5px;
}
.book, .book-img {
  width: 150px;
  height: 200px;
  border-radius: 5px;
}
.book {
  margin: 20px 15px 5px 15px;
}
.border-top {
  border-top: 1px solid #eee !important;
  margin-top: 20px;
  padding-top: 15px;
}
.card {
  margin: 40px 0px;
  padding: 40px 50px;
  border-radius: 20px;
  border: none;
  box-shadow: 1px 5px 10px 1px rgba(0, 0, 0, 0.2);
}
input, textarea {
  background-color: #f3e5f5;
  padding: 8px 15px 8px 15px;
  width: 100%;
  border-radius: 5px !important;
  box-sizing: border-box;
  border: 1px solid #f3e5f5;
  font-size: 15px !important;
  color: #000 !important;
  font-weight: 300;
}
input:focus, textarea:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  border: 1px solid #9fa8da;
  outline-width: 0;
  font-weight: 400;
}
button:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  outline-width: 0;
}
.pay {
  width: 80px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid #673ab7;
  margin: 10px 20px 10px 0px;
  cursor: pointer;
  box-shadow: 1px 5px 10px 1px rgba(0, 0, 0, 0.2);
}
.gray {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  filter: grayscale(100%);
  color: #e0e0e0;
}
.gray .pay {
  box-shadow: none;
}
#tax {
  border-top: 1px lightgray solid;
  margin-top: 10px;
  padding-top: 10px;
}
.btn-blue {
  border: none;
  border-radius: 10px;
  background-color: #673ab7;
  color: #fff;
  padding: 8px 15px;
  margin: 20px 0px;
  cursor: pointer;
}
.btn-blue:hover {
  background-color: #311b92;
  color: #fff;
}
#checkout {
  float: left;
}
#check-amt {
  float: right;
}
@media screen and (max-width: 768px) {
  .book, .book-img {
    width: 100px;
    height: 150px;
 }
 .bookmodal {
  width: 120px;
  height: 110px;
  border-radius: 5px;
}
  .card {
    padding-left: 15px;
    padding-right: 15px;
 }
  .mob-text {
    font-size: 13px;
 }
  .pad-left {
    padding-left: 20px;
 }
}

